.card {
  background-color: whitesmoke;
  padding: 0.5rem;
  margin: 2rem auto;
  width: 50rem;
  display: flex;
  flex-direction: column;
  max-width: 90%;
  border-radius: 12px;
  text-align: left;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

ul {
  list-style-type: numbers;
  font-size: 1.25rem;
}

h3 {
  margin: 0.2rem;
}
